import React, { useState } from 'react'
import {
  Container,
  Icon,
  Menu,
  Responsive,
  Segment,
  Sidebar,
  Divider,
  Visibility,
  Image
} from 'semantic-ui-react'
import Profil from '../asset/pp.jpeg'

const getWidth = () => {
  const isSSR = typeof window === 'undefined'

  return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth
}

const MobileMenu = () => {
  const [activeItem, setActiveItem] = useState();
  const [sidebarOpened, setSidebarOpened] = useState(false);
  const [menuFixed, setMenuFixed] = useState(false);

  const handleSidebarHide = () => setSidebarOpened(false)

  const handleToggle = () => setSidebarOpened(true)

  const handleItemClick = (e, { name }) => setActiveItem(name)

  const stickTopMenu = () => setMenuFixed(true)

  const unStickTopMenu = () => setMenuFixed(false)

  const style = sidebarOpened ? { minHeight: 350, padding: '3em 0em' } : { minHeight: 0, padding: '3em 0em' }
  return (
    <Visibility
      onBottomPassed={stickTopMenu}
      onBottomVisible={unStickTopMenu}
      once={false}
    >
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}
      >
        <Sidebar
          as={Menu}
          animation='push'
          inverted
          onClick={handleSidebarHide}
          vertical
          visible={sidebarOpened}
        >
          <Menu.Item
            style={{ color: '#fff' }}
            header
            href='#home'
            name='home'
            active={activeItem === 'home'}
            onClick={handleItemClick}>
            <Icon name='user circle' size='large' /> &nbsp;&nbsp;Who am I?
          </Menu.Item>

          <Menu.Item
            header
            href='#skillset'
            name='skillset'
            active={activeItem === 'skillset'}
            onClick={handleItemClick}>
            <Icon name='wrench' size='large' />&nbsp;&nbsp;Skillset
          </Menu.Item>

          <Menu.Item
            header
            href='#experiences'
            name='experience'
            active={activeItem === 'experience'}
            onClick={handleItemClick}>
            <Icon name='address book' size='large' />&nbsp;&nbsp;Experiences
          </Menu.Item>

          <Menu.Item
            header
            href='#contact'
            name='contact'
            active={activeItem === 'contact'}
            onClick={handleItemClick}>
            <Icon name='object ungroup outline' size='large' /> &nbsp;&nbsp;Contact
          </Menu.Item>

          <Divider hidden />
          <Menu.Item
            header
            name='close'
            style={{ color: 'red' }}
            onClick={handleSidebarHide}>
            <Icon name='times circle' size='large' color='red' /> &nbsp;&nbsp;Close
          </Menu.Item>
        </Sidebar>

        <Sidebar.Pusher dimmed={sidebarOpened}>
          <Segment
            textAlign='center'
            style={style}
            vertical
          >
            <Container>
              <Menu inverted secondary size='large' fixed='top'>
                <Menu.Item onClick={handleToggle}>
                  <Icon name='sidebar' size='large' />
                </Menu.Item>
                <Menu.Item href='#home' position='right'>
                  <Image src={Profil} size='small' style={{ width: '50px' }} circular />
                </Menu.Item>
              </Menu>
            </Container>
          </Segment>
        </Sidebar.Pusher>
      </Responsive>
    </Visibility>
  )
}


export default MobileMenu;


